import { mapGetters } from "vuex";
import { createHelpers } from "vuex-map-fields";
import { required, url, minLength } from "vuelidate/lib/validators";
import { Money } from "v-money";
import { QField } from "quasar";
const { mapFields } = createHelpers({
  getterType: "events/getField",
  mutationType: "events/updateField",
});

export default {
  name: "EventProfile",
  components: {
    Money,
    QField,
  },
  data() {
    return {
      waiting: false,
      files: null,
      file: null,
      money: {
        decimal: ",",
        thousands: ".",
        prefix: "R$ ",
        precision: 2,
        maxlength: 14,
        masked: false,
      },
    };
  },
  validations: {
    categoryId: {
      required,
    },
    name: {
      required,
    },
    date: {
      required,
    },
    time: {
      required,
    },
    local: {
      required,
    },
    street: {
      required,
    },
    neighborhood: {
      required,
    },
    number: {
      required,
    },
    zipcode: {
      required,
      minLength: minLength(8),
    },
    description: {
      required,
    },
    link: {
      url,
    },
    files: {
      required,
    },
  },
  computed: {
    ...mapFields({
      category: "categorySelected",
      categoryId: "eventForm.categoryId",
      name: "eventForm.title",
      date: "eventForm.date",
      time: "eventForm.time",
      ticket: "eventForm.ticket",
      local: "eventForm.local",
      street: "eventForm.street",
      neighborhood: "eventForm.neighborhood",
      number: "eventForm.number",
      city: "eventForm.city",
      zipcode: "eventForm.zipcode",
      description: "eventForm.description",
      link: "eventForm.link",
      images: "eventForm.imageIds",
    }),
    ...mapGetters({
      categories: "categories/loadCategories",
      user: "users/getCurrentUser",
    }),
    categoryErrorMessage() {
      if (!this.$v.categoryId.required) {
        return "Esse campo é requerido";
      }
      return "";
    },
    nameErrorMessage() {
      if (!this.$v.name.required) {
        return "Esse campo é requerido";
      }
      return "";
    },
    dateErrorMessage() {
      if (!this.$v.date.required) {
        return "Esse campo é requerido";
      }
      return "";
    },
    timeErrorMessage() {
      if (!this.$v.time.required) {
        return "Esse campo é requerido";
      }
      return "";
    },
    localErrorMessage() {
      if (!this.$v.local.required) {
        return "Esse campo é requerido";
      }
      return "";
    },
    streetErrorMessage() {
      if (!this.$v.street.required) {
        return "Esse campo é requerido";
      }
      return "";
    },
    neighborhoodErrorMessage() {
      if (!this.$v.neighborhood.required) {
        return "Esse campo é requerido";
      }
      return "";
    },
    numberErrorMessage() {
      if (!this.$v.number.required) {
        return "Esse campo é requerido";
      }
      return "";
    },
    zipcodeErrorMessage() {
      if (!this.$v.zipcode.required) {
        return "Esse campo é requerido";
      } else if (!this.$v.zipcode.minLength) {
        return "Entre com um cep válido";
      }
      return "";
    },
    descriptionErrorMessage() {
      if (!this.$v.description.required) {
        return "Esse campo é requerido";
      }
      return "";
    },
    linkErrorMessage() {
      if (!this.$v.link.url) {
        return "Entre com uma url válida";
      }
      return "";
    },
    filesErrorMessage() {
      if (!this.$v.files.required) {
        return "É necessário uma imagem.";
      }
      return "";
    },
  },
  methods: {
    updateFiles(files) {
      if (Array.isArray(files) === false || files.length === 0) {
        this.files = null;
      } else if (Array.isArray(this.files) === true) {
        const diff = this.files.filter((file) => files.indexOf(file) === -1);
        if (diff.length === 1 && this.files.length > 1) {
          this.files = files.slice();
        } else if (this.files.length < 3) {
          this.files = diff.concat(files);
        }
      } else {
        this.files = files.slice();
      }
    },
    cancelCreate() {
      // cancela criação do evento ou edição de shortEvent
      this.category = {
        label: "",
        value: "",
        color: "#b8cad4",
      };
    },
    confirmCreate() {
      if (this.waiting) return;
      this.waiting = true;
      this.$v.$touch();
      if (!this.$v.$anyError) {
        this.$store
          .dispatch("images/uploadArray", { files: this.files })
          .then((fileIds) => {
            this.images = fileIds;
            this.$store.dispatch("events/createNewEvent").then(() => {
              this.name = "";
              this.date = "";
              this.time = "";
              this.ticket = "";
              this.site = "";
              this.street = "";
              this.neighborhood = "";
              this.number = "";
              this.city = "";
              this.zipcode = "";
              this.description = "";
              this.link = "";
              this.imgUrl = "";
              this.categoryId = "";
              this.images = [];
            });
            this.$router.push({ name: "Schedule" });
            this.waiting = false;
          })
          .catch((error) => {
            console.log(error);
            this.waiting = false;
          });
      }
    },
  },
};
